html {
  color: aquamarine;
  background-color: black;
}

h2,
h3 {
  margin: 0;
}

.Todo {
  &__welcome {
    margin: 30px 0;
    display: flex;
    justify-content: center;
  }

  &__form {
    margin: 0 auto 50px;
    display: flex;
    flex-wrap: wrap;
    width: 300px;
    gap: 10px;
    justify-content: center;
  }

  &__input {
    width: 100%;
  }

  &__select {
    width: 100%;
  }

  &__list {
    list-style: none;
    padding: 0;
    width: 1000px;
    margin: 0 auto;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }

  &__item {
    background-color: aquamarine;
    border-radius: 10px;
    color: black;
    padding: 15px;

    align-items: center;
    justify-content: center;
  }

  &__title {
    margin-bottom: 15px;
  }

  &__info {
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: center;
  }
}
